@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');


html, body{
  font-family: Poppins, sans-serif;
}
.container{
  border: 1px solid #00F;
  border-radius: 5px;
  padding: 1em;
  max-width: 490px;
  margin: 2rem auto;
}
.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bolder;
  font-size: 15px;
}
.btn{
  padding: 0.7em 1em;
  font-size: 18px;
  border-radius: 5px;
  outline: none;
  border: none;
  color: #FFF;
}
.task{
  user-select: none;
  background-color: #eee;
  margin: 0.5em;
  padding: 0.8em;
  display: flex;
  flex-direction: column;
}
.task h3, .task p{
  margin: 0;
}
.task h3{
  display: flex;
  justify-content: space-between;
}
.reminder{
  border-left: 5px solid #04a70c;
}
.action{
  display: flex;
  gap: 1em;
}

.add-form{
  display: flex;
  flex-direction: column;
  align-items: left;
  font-size: 18px;
  gap: 1em;
  margin: .5em;
}

.add-form .form-control{
  display: flex;
  flex-direction: column;
  font-weight: bolder;
}
.add-form .form-control:last-child{
  flex-direction: row;
}
.add-form .form-control input{
  font-size: 18px;
  padding: 0.5em;
}
.add-form .form-control-check{
  display: flex;
  gap: 1em;
  font-weight: bolder;
}
.add-form .form-control-check input{
  width: 25px;
  height: 25px;
}
.add-form .btn{
  background-color: #000;
  margin-bottom: 1em;
}
